interface Project {
  id: string
  title: string
  description: string
  techStack: string
  imageUrl?: string
  liveUrl?: string
  githubUrl?: string
}

interface ProjectCardProps {
  project: Project
}

const ProjectCard = ({ project }: ProjectCardProps) => {
  const techStackArray = project.techStack.split(',').map(tech => tech.trim())

  return (
    <div className="bg-white rounded-2xl shadow-lg overflow-hidden hover:shadow-2xl transition-all duration-500 transform hover:-translate-y-2 border border-gray-200 group">
      {/* Project Image */}
      <div className="h-48 bg-gradient-to-br from-gray-800 to-black relative overflow-hidden">
        {project.imageUrl ? (
          <img 
            src={project.imageUrl} 
            alt={project.title}
            className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-500"
          />
        ) : (
          <div className="w-full h-full flex items-center justify-center text-white/60">
            <div className="text-center">
              <div className="text-4xl mb-2">üöÄ</div>
              <div className="text-sm">Project Image</div>
            </div>
          </div>
        )}
        <div className="absolute inset-0 bg-black/0 group-hover:bg-black/20 transition-all duration-300"></div>
      </div>
      
      <div className="p-6">
        <h3 className="text-xl font-bold text-black mb-3 group-hover:text-gray-700 transition-colors duration-300">
          {project.title}
        </h3>
        <p className="text-gray-600 mb-4 line-clamp-2">
          {project.description}
        </p>
        <div className="flex flex-wrap gap-2 mb-4">
          {techStackArray.map((tech, index) => (
            <span
              key={index}
              className="bg-black text-white px-3 py-1 rounded-full text-xs font-medium border border-gray-300"
            >
              {tech}
            </span>
          ))}
        </div>
        <div className="flex space-x-4 pt-4 border-t border-gray-200">
          {project.liveUrl && project.liveUrl !== '#' && (
            <a
              href={project.liveUrl}
              className="flex-1 bg-black text-white text-center py-2 rounded-lg font-semibold hover:bg-gray-800 transition-all duration-300 transform hover:scale-105"
            >
              Live Demo
            </a>
          )}
          {project.githubUrl && project.githubUrl !== '#' && (
            <a
              href={project.githubUrl}
              className="flex-1 border-2 border-black text-black text-center py-2 rounded-lg font-semibold hover:bg-black hover:text-white transition-all duration-300 transform hover:scale-105"
            >
              GitHub
            </a>
          )}
        </div>
      </div>
    </div>
  )
}

export default ProjectCard