import { useState } from 'react'
import Head from 'next/head'
import Layout from '../components/Layout'
import ProjectCard from '../components/ProjectCard'
import ContactSection from '../components/ContactSection'
import AdminPanel from '../components/AdminPanel'

const Home = () => {
  const [showAdmin, setShowAdmin] = useState(false)

  const sampleProjects = [
    {
      id: '1',
      title: 'E-Commerce Platform',
      description: 'A full-stack e-commerce solution with modern UI and secure payments',
      techStack: 'React, Node.js, MongoDB, Stripe',
      imageUrl: '/images/ecommerce.jpg',
      liveUrl: '#',
      githubUrl: '#'
    },
    {
      id: '2',
      title: 'Task Management App',
      description: 'A productivity app with real-time collaboration features',
      techStack: 'Next.js, TypeScript, Tailwind CSS, Socket.io',
      imageUrl: '/images/taskapp.jpg',
      liveUrl: '#',
      githubUrl: '#'
    },
    {
      id: '3',
      title: 'Portfolio Website',
      description: 'Modern responsive portfolio with admin dashboard',
      techStack: 'Next.js, TypeScript, Prisma, SQLite',
      imageUrl: '/images/portfolio.jpg',
      liveUrl: '#',
      githubUrl: '#'
    }
  ]

  const skills = [
    { name: 'React/Next.js', level: 90 },
    { name: 'TypeScript', level: 85 },
    { name: 'Node.js', level: 80 },
    { name: 'Tailwind CSS', level: 95 },
    { name: 'Database Design', level: 75 },
    { name: 'UI/UX Design', level: 70 }
  ]

  return (
    <Layout>
      <Head>
        <title>Ogbeifun Daniel Osewe - Full Stack Developer</title>
        <meta name="description" content="Professional portfolio of Ogbeifun Daniel Osewe - Full Stack Web Developer" />
      </Head>

      {/* Admin Toggle Button */}
      <button
        onClick={() => setShowAdmin(!showAdmin)}
        className="fixed top-20 right-4 z-50 bg-black text-white px-4 py-2 rounded-lg text-sm font-medium hover:bg-gray-800 transition-colors duration-300 shadow-lg"
      >
        {showAdmin ? 'View Portfolio' : 'Admin Panel'}
      </button>

      {showAdmin ? (
        <AdminPanel />
      ) : (
        <>
          {/* Hero Section */}
          <section className="min-h-screen flex items-center justify-center relative overflow-hidden bg-gradient-to-br from-gray-50 to-white">
            <div className="absolute inset-0 bg-[radial-gradient(ellipse_at_center,_var(--tw-gradient-stops))] from-gray-200/50 to-transparent"></div>
            <div className="container mx-auto px-4 text-center relative z-10 animate-fade-in">
              <div className="animate-float">
                <div className="w-32 h-32 mx-auto mb-8 bg-gradient-to-br from-black to-gray-800 rounded-full flex items-center justify-center text-white text-4xl font-bold shadow-2xl">
                  DO
                </div>
              </div>
              <h1 className="text-5xl md:text-7xl font-bold text-black mb-6 animate-slide-up">
                Ogbeifun Daniel <span className="text-gray-600">Osewe</span>
              </h1>
              <p className="text-xl md:text-2xl text-gray-600 mb-8 animate-slide-up animation-delay-200">
                Full Stack Web Developer
              </p>
              <p className="text-lg text-gray-500 max-w-2xl mx-auto mb-12 animate-slide-up animation-delay-400">
                I create digital experiences that are fast, responsive, and user-friendly. 
                Specializing in modern web technologies and clean, efficient code.
              </p>
              <div className="flex flex-col sm:flex-row gap-4 justify-center items-center animate-slide-up animation-delay-600">
                <a
                  href="#projects"
                  className="bg-black text-white px-8 py-4 rounded-lg font-semibold hover:bg-gray-800 transition-all duration-300 transform hover:scale-105 shadow-lg"
                >
                  View My Work
                </a>
                <a
                  href="#contact"
                  className="border-2 border-black text-black px-8 py-4 rounded-lg font-semibold hover:bg-black hover:text-white transition-all duration-300 transform hover:scale-105"
                >
                  Get In Touch
                </a>
              </div>
            </div>
          </section>

          {/* Skills Section */}
          <section id="skills" className="py-20 bg-gray-50">
            <div className="container mx-auto px-4">
              <h2 className="text-4xl font-bold text-center text-black mb-16">Technical Skills</h2>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-8 max-w-4xl mx-auto">
                {skills.map((skill, index) => (
                  <div key={skill.name} className="animate-slide-up" style={{ animationDelay: `${index * 100}ms` }}>
                    <div className="flex justify-between mb-2">
                      <span className="font-semibold text-gray-800">{skill.name}</span>
                      <span className="text-gray-600">{skill.level}%</span>
                    </div>
                    <div className="w-full bg-gray-200 rounded-full h-3">
                      <div 
                        className="bg-black h-3 rounded-full transition-all duration-1000 ease-out"
                        style={{ width: `${skill.level}%` }}
                      ></div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </section>

          {/* Projects Section */}
          <section id="projects" className="py-20 bg-white">
            <div className="container mx-auto px-4">
              <h2 className="text-4xl font-bold text-center text-black mb-4">Featured Projects</h2>
              <p className="text-gray-600 text-center mb-12 max-w-2xl mx-auto">
                A collection of projects that showcase my skills and passion for web development
              </p>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                {sampleProjects.map((project, index) => (
                  <div key={project.id} className="animate-slide-up" style={{ animationDelay: `${index * 200}ms` }}>
                    <ProjectCard project={project} />
                  </div>
                ))}
              </div>
            </div>
          </section>

          {/* About Section */}
          <section id="about" className="py-20 bg-gray-50">
            <div className="container mx-auto px-4">
              <div className="max-w-4xl mx-auto">
                <h2 className="text-4xl font-bold text-center text-black mb-12">About Me</h2>
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-center">
                  <div className="animate-slide-up">
                    <div className="bg-white p-8 rounded-2xl shadow-lg border border-gray-200">
                      <h3 className="text-2xl font-bold text-black mb-4">My Journey</h3>
                      <p className="text-gray-600 mb-6">
                        I'm a passionate full-stack developer with a keen eye for design and user experience. 
                        My journey in web development started with curiosity and has evolved into a career 
                        dedicated to creating exceptional digital experiences.
                      </p>
                      <p className="text-gray-600 mb-6">
                        I believe in writing clean, maintainable code and staying updated with the latest 
                        technologies and best practices in the industry.
                      </p>
                      <div className="flex flex-wrap gap-3">
                        {['React', 'TypeScript', 'Node.js', 'Next.js', 'Tailwind', 'Prisma'].map((tech) => (
                          <span key={tech} className="bg-black text-white px-3 py-1 rounded-full text-sm font-medium">
                            {tech}
                          </span>
                        ))}
                      </div>
                    </div>
                  </div>
                  <div className="animate-slide-up animation-delay-300">
                    <div className="bg-gradient-to-br from-black to-gray-800 p-8 rounded-2xl text-white shadow-2xl">
                      <h3 className="text-2xl font-bold mb-4">What I Do</h3>
                      <ul className="space-y-3">
                        <li className="flex items-center">
                          <div className="w-2 h-2 bg-white rounded-full mr-3"></div>
                          Full-stack web application development
                        </li>
                        <li className="flex items-center">
                          <div className="w-2 h-2 bg-white rounded-full mr-3"></div>
                          Responsive and mobile-first design
                        </li>
                        <li className="flex items-center">
                          <div className="w-2 h-2 bg-white rounded-full mr-3"></div>
                          Database design and optimization
                        </li>
                        <li className="flex items-center">
                          <div className="w-2 h-2 bg-white rounded-full mr-3"></div>
                          API development and integration
                        </li>
                        <li className="flex items-center">
                          <div className="w-2 h-2 bg-white rounded-full mr-3"></div>
                          Performance optimization
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>

          {/* Contact Section */}
          <ContactSection />
        </>
      )}
    </Layout>
  )
}

export default Home